import Head from "next/head";
import Icon from "../helper/Icon";
export default () => (
  <ex>
    <header>
    <Icon name="price" width={50} />
    <Icon name="find" />
    <Icon name="basket" />
    <Icon name="order" />
    <Icon name="card" />
    </header>
    <style jsx>{`
    header   {
        display: flex;
        justify-content: center;
        background-color:red;
        
      }
    `}</style>
  </ex>
);



